.terms_filters_store label.fl_chek span {
  white-space: normal !important;
  line-height: 13px;
  font-size: 13px;
  display: block;
  max-width: 191px;
}
.main_filters {
  margin-bottom: 20px;
}
.ncatlist #mse2_marker_0 + label:before {
  background: url("data:image/png;base64,")
    no-repeat center center;
  background: 100% auto;
}
.ncatlist #mse2_marker_0:checked + label:before {
  background: url("data:image/png;base64,")
    no-repeat center center;
  background: 100% auto;
}
@media screen and (min-width: 768px) {
  .ncatlist .ncatcheckbox + label:before {
    //top: 7px;
  }
}
@media screen and (max-width: 768px) {
  .main_filters .clean_filters {
    display: none;
  }

  .ncatlist .newfilterj .ncatfiltersector.ncatfiltersectorbtn {
    padding: 0;
  }

  .ncatlist .ncatfiltersectorbtn .df {
    width: calc(100%);
  }

  .ncatslrange {
    margin-right: 15px !important;
    margin-left: 20px !important;
    margin-top: 20px !important;
  }
  .ncatslrange .noUi-horizontal .noUi-handle {
    width: 25px;
    height: 25px;
  }
  .ncatslrange .noUi-connects {
    height: 12px;
  }

  #mse2_filters .mse2_number_slider {
    margin-bottom: 36px;
  }
}

.select_li_choice_parent.selected {
  font-weight: 700;
}

.catalog-list {
  &__filter {
    @media (min-width: 992px) {
      padding: 0;
      width: 300px;
    }
  }

  &__items {
    @media (min-width: 992px) {
      padding: 0 0 0 20px;
      width: calc(100% - 300px);
    }
  }

  .grid-view.wishlist-items {
    @media (min-width: 992px) and (max-width: 1199px) {
      grid-template-columns: 1fr 1fr !important;
    }

    @media (min-width: 1200px) and (max-width: 1440px) {
      grid-template-columns: 1fr 1fr 1fr !important;
    }
  }
}

// From default/_custom.scss
.filters_product_banner {
  text-align: center;
  width: 100%;
  margin-top: 10px;
  margin-bottom: 5px;
}

.filters_product_banner img {
  width: 100%;
}

// From inline page styles
.filters_product_banner {
  height: 275px;
  grid-row-start: 8;
  grid-row-end: 3;
  grid-column-start: 1;
  grid-column-end: 5;
}

.grid-view .filters_product_banner {
  height: auto;
  margin-top: 20px;
  grid-row-start: 3;
  grid-column-end: 4;
  border-bottom: 1px solid #ccc;

  @media (min-width: 992px) {
    grid-column-end: 3;
    margin-top: 0;
  }

  @media (min-width: 1200px) {
     grid-column-end: 4;
  }

  @media (min-width: 1440px) {
    grid-column-end: 5;
  }
}

.list-view .filters_product_banner {
  grid-column-start: 1;
  grid-column-end: 1;
  border-top: 1px solid #ccc;
}

.grid-view .filters_product_banner img {
  margin-bottom: 20px;

  @media (min-width: 992px) {
    margin-bottom: 40px;
  }
}

.list-view .filters_product_banner img {
  margin-top: 20px;
}

@media (max-width: 767px) {
  .filters_product_banner {
    display: none !important;
  }
}
