%letter {
  letter-spacing: -0.01px;
}

$borderSepColor: #ccc;

@import "badge";

//  
.in_stock {
  .item-storage__name {
    color: #5DAC0D;
  }
}

//   
.no_stock {
  .item-storage__name {
    color: #CC2500;
  }
}

.ecommerce {
  @media screen and (max-width: 320px) {
    width: 320px;
    min-width: 320px;
  }
}

$search-bar-height: 48px;
.ecommerce-application {
  .content-body {
    position: relative;
  }

  // To overlay in whole page
  .body-content-overlay {
    position: fixed !important;
    z-index: 12 !important;
  }

  // Shop Sidebar
  .sidebar-shop {
    margin-top: 0.85rem;
    width: $sidebar-width;
    z-index: 14;

    // for heading
    .filter-heading {
      margin-bottom: 1.75rem;
    }

    // for title
    .filter-title {
      margin-bottom: 1rem;
      margin-top: 2.5rem;
    }

    // Sidebar price range and categories list
    .price-range,
    .categories-list {
      li:not(:last-child) {
        margin-bottom: 0.75rem;
      }
    }

    // Sidebar brand list
    .brand-list {
      li {
        display: flex;
        justify-content: space-between;
        align-items: center;
        margin-bottom: 0.75rem;
      }
    }

    // Sidebar for slider
    .range-slider.noUi-horizontal {
      .noUi-handle {
        .noUi-tooltip {
          opacity: 0;
          transform: translate(-50%, -15%);

          &:before {
            content: '$ ';
          }
        }

        &:active {
          .noUi-tooltip {
            opacity: 1;
          }
        }
      }
    }

    // for stars in rating
    .ratings-list {
      display: flex;
      justify-content: space-between;
      margin-bottom: 0.5rem;

      &:last-child {
        margin-bottom: 2.5rem;
      }

      ul {
        margin-bottom: 0;

        .ratings-list-item {
          svg,
          i {
            width: 1.25rem;
            height: 1.25rem;
            font-size: 1.25rem;
          }
        }
      }
    }
  }

  // ratings style
  .filled-star {
    fill: $warning;
    stroke: $warning;
    color: $warning;
  }

  .unfilled-star {
    stroke: $gray-100;
    color: $gray-100;
  }

  // E-commerce shop main content area

  //ecommerce header
  .ecommerce-header-items {
    display: flex;
    justify-content: space-between;

    .result-toggler {
      display: flex;
      align-items: center;

      // Sidebar toggler
      .shop-sidebar-toggler {
        padding-left: 0;

        &:active,
        &:focus {
          outline: 0;
        }

        .navbar-toggler-icon {
          height: auto;

          i,
          svg {
            color: $body-color;
            height: 1.5rem;
            width: 1.5rem;
            font-size: 1.5rem;
          }
        }
      }

      // Search result title
      .search-results {
        font-weight: $font-weight-bold;
        color: $headings-color;
      }
    }
  }

  // Product search bar
  .ecommerce-searchbar {
    .input-group {
      box-shadow: 0 2px 8px 0 rgba($black, 0.14);
      border-radius: 0.3rem;
    }
  }

  // Search bar
  .search-product,
  .input-group-text {
    height: $search-bar-height;
    border: none;
    font-size: 0.95rem;
    padding-left: 1.25rem;

    &::placeholder {
      font-size: 0.95rem;
    }
  }

  // main Products
  .ecommerce-card {

    // ratings
    .item-rating {
      ul {
        margin-bottom: 0;
      }

      svg,
      i {
        height: 1.143rem;
        width: 1.143rem;
        font-size: 1.143rem;
      }
    }

    // Item name
    .item-name {
      margin-bottom: 8px;
      @include mobile() {
        margin-bottom: 5px;
      }

      a {
        font-size: 16px;
        letter-spacing: -0.01px;
        line-height: 19px;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        transition: 0.3s;


        @include mobile() {
          -webkit-line-clamp: 3;
          font-size: 12px;
          line-height: 120%;
        }
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;

        &:hover {
          color: $primary;
        }
      }
    }

    .item-storage {

      display: flex;
      flex-direction: column;
      align-items: flex-start;
      padding: 0;
      margin: 10px 0;
      height: 40px;
      @include mobile() {
        margin-bottom: 5px;
        margin-top: 5px;
      }

      &__name {
        @extend %letter;
        font-size: 14px;

        @include mobile() {
          font-size: 10px;

          height: 14px;
          line-height: 12px;

        }

        line-height: 120%;
        height: 20px;
      }
    }

    .item-prices {
      height: 45px;
      flex: none;
      order: 3;
      flex-grow: 0;
      margin: 30px 0 0 0;

      &__mod {
        display: flex;
        flex-direction: row;
        align-items: center;
        height: 15px;
        font-size: 12px;

        @include mobile() {
          font-size: 10px;
        }
        padding: 1px 2px;

        &__sale_price, &__old_price {
          display: flex;
          align-items: center;
          text-align: center;
          flex-direction: column;
          flex-grow: 0;
          flex: none;
        }

        &__old_price {
          margin: 2px 2px 2px 0;
          order: 0;
          color: #999999;
          text-decoration-line: line-through;
          font-style: italic;
        }

        &__sale_price {
          background: #FFE600;
          border-radius: 3px;
          order: 1;
          margin: 0 0 0 10px;
          height: 15px;
          padding: 1px 2px;


          span {
            @extend %letter;
            font-weight: bold;
            line-height: 12px;
            display: flex;
            align-items: center;
            text-align: center;
            color: #000000;
            flex: none;
            order: 0;
            flex-grow: 0;
            margin: 1px 0;
          }
        }

      }


      &__price {
        @extend %letter;
        padding: 6px 0 5px 0;
        height: 30px;
        font-size: 18px;
        line-height: 20px;
        font-weight: bold;
        @include mobile() {
          font-size: 23px;
        }
      }
    }

    .item-article {
      color: #999999;
      height: 30px;
      font-size: 15px;
      line-height: 18px;
      display: flex;
      align-items: center;

      @include mobile() {
        height: 24px;
      }

      .tooltip {
        .tooltip-inner {
          height: 30px;
          font-size: 10px;
          line-height: 90%;
          display: flex;
          align-items: center;
        }
      }

      &__text {
        font-size: 15px;
        line-height: 18px;
        letter-spacing: -0.01px;
        color: #999999;

       margin-left: 5px;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        text-overflow: ellipsis;

        @include mobile() {
          font-size: 12px;
        }

      }

      &__3dmodel,&__yottube {
        width: 30px;
        height: 30px;
        position: relative;

        display: flex;
        align-items: center;
        flex: none;
        order: 0;
        flex-grow: 0;


        @include mobile() {
          margin-right: 2px;
          margin-left: -3px;
        }

        &:after {
          position: absolute;
          content: "";
          width: 30px;
          height: 30px;
          background-repeat: no-repeat;
          background-image: url("data:image/svg+xml;base64,");
        }

        &:hover {
          &:after {
            background-image: url("data:image/svg+xml;base64,");
          }
        }
      }

      &__yottube {
        cursor: pointer;
        &:after {
          background-image: url("data:image/svg+xml;base64,");
        }
        &:hover {
          &:after {
            background-image: url("data:image/svg+xml;base64,");
          }
        }
      }
    }




    .item-description {
      font-size: 0.875rem;
    }

    // buttons style
    .btn-wishlist,
    .btn-cart {
      @extend %letter;
      color: #fff;
      border-radius: 2px;
      font-size: 14px;
      line-height: 14px;
      padding: 12px 20px;
      height: 40px;
      width: 137px;
      text-align: center;

      font-weight: 300;
    }


    .item-buttons {
      flex: none;
      order: 4;
      flex-grow: 0;
      margin: 10px 0;
    }

    .btn-cart, .item-list {
      float: left;
    }

    .item-list {
      padding: 6px 0 0 0;

      &__item {
        width: 30px;
        height: 30px;
        margin: 0 5px;
        position: relative;
        display: inline-block;
        cursor: pointer;

        @include mobile() {
          margin: 0 28px 0 0;
        }

        &:after {
          content: "";
          position: absolute;
          width: 30px;
          height: 30px;
          left: 5px;
          background-repeat: no-repeat;

          @include mobile() {
            left: 0;
          }
        }
      }

      .btn-favorite {
        &:after {
          background-image: url("data:image/svg+xml;base64,");
        }

        &.voted {
          &:after {
            background-image: url("data:image/svg+xml;base64,") !important;
          }
        }
      }

      .btn-compare {
        &:after {
          background-image: url("data:image/svg+xml;base64,");
        }

        &.added {
          &:after {
            background-image: url("data:image/svg+xml;base64,") !important;
          }
        }
      }
    }

    .item-info {
      display: none;
    }

    .item-dots {
      position: absolute;
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      z-index: 2;
    }

    .item-images {
      display: flex;
      width: 90%;
      height: 100%;
      margin: 0 auto;
      z-index: 9;
      left: 0;
      top: 0;
      flex-direction: row;

      @include mobile() {
        top: 224px;
        height: auto;
      }

      &__image_hide {
        display: none;
      }

      &__zone {
        flex-grow: 1;
        height: 100%;
      }
    }

    .item-slider {
      position: absolute;
      /*bottom: -50px;*/
      display: -webkit-box;
      display: -webkit-flex;
      display: -moz-box;
      display: -ms-flexbox;
      display: flex;
      width: 100%;
      left: 0;
      cursor: default;
      -webkit-box-pack: center;
      -webkit-justify-content: center;
      -moz-box-pack: center;
      -ms-flex-pack: center;
      justify-content: center;
      margin-top: 20px;

      &__dot {
        width: 7px;
        height: 7px;
        border-radius: 50%;
        background: #999;
        margin: 0 5px;
        border: 1px solid #fff;
        -webkit-transition: .2s;
        transition: .2s;
        cursor: pointer;

        &.active {
          position: relative;
          background: #de5617;
          border: 1px solid #de5617;
        }
      }
    }

    .item-badges {
      display: flex;
      align-items: center;
      column-gap: 5px;
      position: absolute;
      bottom: 0;
      left: 0;
      width: auto;
      height: 24px;
      z-index: 3;

      &__el {
        display: inline-block;
        position: relative;
        height: 24px;
        background-position: center;
        background-repeat: no-repeat;
        cursor: pointer;

        &_water-drop {
          width: 48px;
          background-image: url("data:image/svg+xml;base64,");
        }

        &_tv {
          width: 25px;
          background-image: url("data:image/svg+xml;base64,");
        }
      }

      .tooltip-inner {
        font-size: 10px;
        line-height: 12px;

        @media (min-width: 1200px) {
          white-space: nowrap;
        }
      }
    }

    // Item Image
    .item-img {
      text-align: center;
      margin-top: 33px;


      // no drag body
      user-select: none;
      touch-action: pan-y;

      &__water-drop-badge {
        display: block;
        position: absolute;
        bottom: 0;
        left: 0;
        width: 48px;
        height: 24px;
        background: url("data:image/svg+xml;base64,") center no-repeat;
        z-index: 3;
        cursor: pointer;

        @media (min-width: 1200px) {
          overflow: hidden;

          &:hover {
            overflow: initial;

            span {
              opacity: 1;
              right: 0;
            }
          }
        }

        span {
          display: flex;
          justify-content: center;
          align-items: center;
          position: absolute;
          top: -3px;
          left: 0;
          z-index: 1;
          width: 92px;
          height: auto;
          transform: translateY(calc(-100% - 9px));
          min-height: 34px;
          margin: 0;
          padding: 5px;
          background: #000;
          border-radius: 2px;
          font-style: normal;
          font-weight: 400;
          font-size: 10px;
          line-height: 12px;
          text-align: center;
          color: #fff;
          opacity: 0;
          transition: all .2s linear;

          &.visible {
            @media (max-width: 1199px) {
              top: 0;
              opacity: 1;
            }
          }

          @media (min-width: 1200px) {
            width: initial;
            height: 24px;
            min-height: initial;
            padding: 5px 7.02px;
            white-space: nowrap;
            top: 50%;
            left: initial;
            right: -3px;
            transform: translate(calc(100% + 7px), -50%);
          }

          &::before {
            content: '';
            display: block;
            position: absolute;
            width: 14px;
            height: 9px;
            bottom: 0;
            left: 50%;
            transform: translate(-50%, 8px);
            background: url("https://9d2c73b6-1b6d-45e9-9faa-7fb794647734.selcdn.net/i/380/7d47a7277a.png") center no-repeat;

            @media (min-width: 1200px) {
              width: 8px;
              height: 13px;
              top: 50%;
              bottom: initial;
              left: 0;
              transform: translate(-7px, -50%);
              background-image: url("https://9d2c73b6-1b6d-45e9-9faa-7fb794647734.selcdn.net/i/381/5addd360c0.png");
            }
          }
        }
      }

      @include desktop() {
        min-height: 15.85rem;
        &__list {
          min-height: 224px;
        }
      }

      position: relative;

      &__list {
        display: flex;
        justify-content: center;
        @include mobile() {
          min-height: 140px;
        }
      }
    }
  }

  // margin for both views
  .grid-view,
  .list-view {
    &:not(.wishlist-items) {
      margin-top: 2rem;
    }
  }

  .collections-view {
    display: flex;
    flex-wrap: wrap;

    @media (min-width: 768px) {
      column-gap: 40px;
    }

    @media (min-width: 1200px) {
      padding-top: 20px;
      column-gap: 20px;
    }

    .ecommerce-card {
      //border-radius: 4px;
      border-radius: 0;
      border-color: transparent;
      width: 100%;
      box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
      transition: all .17s linear;

      @media (min-width: 768px) {
        width: calc(50% - 40px / 2);
      }

      @media (min-width: 992px) {
        &:nth-child(odd)::after {
          left: 0;
          right: initial;
        }

        &:nth-child(even)::after {
          left: initial;
          right: 0;
        }
      }

      @media (min-width: 1200px) {
        width: calc(50% - 20px / 2);
        padding: 19px;
        margin-bottom: 40px;

        @media (min-width: 1200px) {
           &:hover {
             box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
           }
        }
      }

      &::after {
        content: "";
        display: block;
        position: absolute;
        top: initial;
        bottom: 0;
        left: 50%;
        transform: translateX(-50%);
        width: calc(100% + 42px);
        height: 1px;
        background-color: #bababa;

        @media (min-width: 992px) {
           transform: none;
        }

        @media (min-width: 1200px) {
          bottom: -21px;
        }
      }

      &__body {
        display: flex;
        flex-wrap: wrap;
        justify-content: space-between;
        width: 100%;
      }

      .item-img {
        width: calc(50% - 9px);
        margin-top: 54px;

        @media (min-width: 1200px) {
          width: calc(50% - 10px);
          margin-top: 30px;
          padding-right: 20px;
        }

        &__thumb-wrap {
          display: block;
          position: relative;
          width: 150px;
          height: 140px;

          @media (min-width: 1200px) {
            width: 100%;
            height: auto;

            &::before {
              content: "";
              display: block;
              padding-bottom: 100%;
            }
          }
        }

        &__thumb {
          width: 100%;
          height: 100%;
          object-fit: contain;

          @media (min-width: 1200px) {
            display: block;
            position: absolute;
            top: 0;
            left: 0;
          }
        }
      }

      .card-content {
        width: calc(50% - 9px);
        margin-top: 20px;

        @media (min-width: 1200px) {
          width: calc(50% + 10px);
          margin-top: 2px;
          padding-left: 20px;
          padding-right: 20px;
        }
      }

      .card-body {
        padding: 0;
      }

      .card-collection {
        width: 100%;
        height: auto;
        border-top: 1px solid #bababa;
        margin-top: 12px;
        padding-top: 18px;
        padding-bottom: 26px;

        @media (min-width: 1200px) {
          margin-top: 19px;
          padding-top: 19px;
          padding-bottom: 0px;
        }

        p {
          display: flex;
          align-items: baseline;
          column-gap: 30px;
          padding: 0;
          margin: 0 0 10px 0;
          font-style: normal;
          font-weight: 400;
          font-size: 12px;
          line-height: initial;
          color: #000000;

          @media (min-width: 1200px) {
            column-gap: 37px;
            font-size: 14px;
            line-height: 24px;
            margin-bottom: 7px;
          }

          a {
            color: #00aae9;
            text-decoration: none;
            transition: color .1s linear;

            @media (min-width: 1200px) {
               &:hover {
                 color: #de5617;
               }
            }
          }
        }

        ul {
          display: flex;
          align-items: flex-start;
          column-gap: 10px;
          width: 100%;
          height: auto;
          list-style: none;
          margin: 0;
          padding: 0;

          @media (min-width: 1200px) {
            column-gap: 22px;
          }

          li  {
            width: 100%;

            &:nth-child(n+5) {
              display: none;
            }

            @media (min-width: 1200px) {
              display: block !important;

              &:nth-child(n+6) {
                display: none !important;
              }
            }

            a {
              display: flex;
              flex-direction: column;
              align-items: center;
              width: 100%;

              @media (min-width: 1200px) {
                 &:hover {
                   img {
                     opacity: 0.7;
                   }

                   span {
                     color: #00aae9;
                   }
                 }
              }
            }

            div {
              width: 100%;
              height: auto;
              position: relative;

              &::before {
                content: '';
                display: block;
                padding-bottom: 100%;
              }
            }

            img {
              position: absolute;
              top: 0;
              left: 0;
              width: 100%;
              height: 100%;
              object-fit: contain;
              transition: all .1s linear;
            }

            span {
              display: block;
              margin-top: 11px;
              font-style: normal;
              font-weight: 400;
              font-size: 12px;
              line-height: 14px;
              letter-spacing: 0.01em;
              color: #000000;
              text-align: center;
              transition: all .1s linear;

              @media (min-width: 1200px) {
                margin-top: 11px;
              }
            }
          }
        }
      }

      .item-label {
        left: 0;
        width: calc(50% - 13px);

        @media (min-width: 1200px) {
          top: 19px;
          left: 19px;
        }
      }

      .item-name {
        height: 42px;

        @media (min-width: 1200px) {
            height: 36px;
            margin-bottom: 12px;
        }

        a {
          font-size: 12px;
          line-height: 14px;
          -webkit-line-clamp: 3;

          @media (min-width: 1200px) {
            font-size: 16px;
            line-height: 18px;
            -webkit-line-clamp: 2;
          }
        }
      }

      .item-article__text {
        font-size: 12px;

        @media (min-width: 1200px) {
          font-size: 16px;
          line-height: 18px;
        }
      }

      .item-prices {
        @media (min-width: 1200px) {
          margin-bottom: 6px;
        }
      }

      .item-prices__price {
        padding-top: 5px;
        font-size: 24px;

        @media (min-width: 992px) {
          padding-top: 6px;
          font-size: 18px;
          line-height: 20px;
        }
      }

      .item-storage {
        margin-bottom: 6px;
        margin-top: 3px;

        @media (min-width: 1200px) {
          margin-top: 11px;
          margin-bottom: 48px;
        }
      }

      .item-buttons {
        margin-top: 11px;

        @media (min-width: 1200px) {
          margin: 0;
        }
      }

      .btn-cart {
        width: 150px;
        border-radius: 3px;

        @media (min-width: 1200px) {
          flex: 1 0 auto;
          width: 124px;
          max-width: 124px;
          margin-right: 5px;
        }

        @media (min-width: 1440px) {
          margin-right: 12px;
        }
      }

      .item-list {
        width: 100%;
        padding-top: 11px;

        @media (min-width: 1200px) {
          width: auto;
          padding-top: 0;
          display: flex;
          align-items: center;
        }
      }

      .item-buttons__btn {
        @media (min-width: 1200px) {
          display: flex;
          align-items: center;
        }
      }

      .item-list__item {
        margin-right: 7px;

        @media (min-width: 1200px) {
          margin-right: 5px;
          margin-left: 0;
        }

        @media (min-width: 1440px) {
          margin-right: 14px;
        }
      }

      .item-badges {
        bottom: 8px;
      }

      .item-storage__name {
        height: 14px;
        font-size: 10px;
        line-height: 12px;

        @media (min-width: 1200px) {
          height: 20px;
          font-size: 14px;
          line-height: 18px;
        }
      }
    }
  }

  .cards-view {

    .card-body, .item-buttons, .item-img {
      @media (min-width: 1200px) {
        position: relative;
        z-index: 1;
      }
    }

    .item-label {
      top: 15px;
    }

    .item-storage {
      @media (min-width: 1200px) {
        margin: 12px 0 10px;
      }
    }

    .ecommerce-card {
      @media (min-width: 1200px) {
        padding: 0 !important;

        &::after {
          bottom: 20px !important;
        }

        &:nth-child(3n+3)::after {
          left: initial !important;
          right: 0 !important;
        }
      }

      @media (min-width: 1400px) {
        &:nth-child(3n+3)::after {
          left: 0 !important;
          right: initial !important;
        }

        &:nth-child(4n+4)::after {
          left: initial !important;
          right: 0 !important;
        }
      }
    }

    .ecommerce-card__body {
      @media (min-width: 1200px) {
        position: relative;
        width: 100% !important;
        padding: 15px 16px 15px 15px;

        &::before {
          content: "";
          position: absolute;
          top: 0;
          left: 0;
          z-index: 0;
          width: 100%;
          height: 100%;
          //border-radius: 4px;
          box-shadow: 0 0 0 rgba(0, 0, 0, 0);
          transition: all .17s linear;
        }

        &:hover::before {
          box-shadow: 0 0 20px rgba(0, 0, 0, 0.25);
        }
      }
    }

    &.list-view {

      .ecommerce-card {
        @media (min-width: 1200px) {
          padding-top: 10px !important;
          margin-top: 23px;
        }
      }

      .ecommerce-card__body {
        @media (min-width: 1200px) {
          &::before {
            top: 10px;
          }
        }
      }
    }
  }
  
  // Grid View Specific style
  .grid-view {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    column-gap: 20px;

    &.wishlist-items {
      grid-template-columns: 1fr 1fr 1fr 1fr;
    }

    &.wishlist-items-inline {
      display: flex;
      margin-top: 0 !important;

      .ecommerce-card {
        flex-shrink: 0;
        position: relative;
        transition-property: transform;
        padding: 20px 0;
        max-width: 244px;
        min-width: 150px;

        @include mobile() {
          max-width: 160px;
        }
      }
    }

    .wishlist-slide {
      .ecommerce-card {
        flex-shrink: 0;
        width: 100%;
        height: 100%;
        position: relative;
        transition-property: transform;
      }
    }

    .ecommerce-card {
      @include media-breakpoint-up(lg) {
        min-width: 224px !important;
      }

      border-width: 0;
      border-radius: 0;
      padding: 20px 0 20px 0;

      // Item name
      .item-name {
        margin-top: 6px;
        height: 42px;
        @include mobile() {
          margin-top: 12px;
        }
      }

      @include mobile() {
        padding-bottom: 16px;
      }

      .btn-cart, .item-list {
        @include mobile() {
          float: none;
        }
      }

      @include mobile() {
        .btn-cart {
          width: 100%;
        }

        .item-img {
          img {
            width: 140px;
          }
        }
      }

      &__body {
        width: 100%;
        @include media-breakpoint-up(md) {
          width: 224px;
        }
      }


      // Item content wrapper
      .item-wrapper {
        display: flex;
        justify-content: space-between;
        align-items: baseline;
      }

      // Hiding list view items
      .shipping,
      .item-company,
      .item-options .item-price {
        display: none;
      }

      .card-body {
        padding: 17px 0 0 0;
      }

      .item-list {
        height: 33px;
        @include mobile() {
          padding: 13px 0 0 0;
        }
      }

      .item-buttons {
        .item-prices {
          display: none;
        }
      }
    }
  }

  // List View Specific style
  .list-view {
    display: grid;
    grid-template-columns: 1fr;

    .ecommerce-card {
      border-width: 0;
      border-radius: 0;
      padding-top: 20px;
      margin-top: 20px;
      padding-bottom: 20px;
      min-height: 301px;

      &:first-child {
        margin-top: 0;
      }


      @include mobile() {
        padding-bottom: 10px;
        margin-top: 10px;
      }

      > .d-flex {
        display: block !important;
        width: 100%;
      }

      .ecommerce-card__body {
        display: grid;
        grid-template-columns: 1fr 2fr 1fr;
        border-width: 0;
        border-radius: 0;
      }

      @include desktop() {
        border-top: 1px solid $borderSepColor;
        .item-img {
          max-height: 244px;
          max-width: 224px;
          margin-right: 20px;
          margin-left: 20px;
          margin-top: 19px;
        }
      }

      &:last-child {
        border-bottom: 1px solid $borderSepColor;
      }


      // list view image

      .card-body {
        padding: 0 0 0 0;
        display: flex;
        flex-direction: column;

        .item-prices {
          display: none;
        }

        // item name and ratings order
        .item-wrapper {
          order: 2;
        }

        .item-name {
          margin-top: 20px;
        }

        // .item-rating,
        .item-price {
          display: none;
        }
      }

      .item-img {
        @include mobile() {
          margin-bottom: 34px;
          margin-top: 20px;
        }
      }

      .item-slider {
        @include mobile() {
          margin-top: 9px;
        }
      }

      .item-buttons {
        @extend .d-flex;
        @extend .align-items-end;
        @extend .flex-column;

        @include desktop() {
          padding: 0 20px;
          margin-bottom: 0;
        }


        &__btn {
          width: 100%;
          @include media-breakpoint-up(xl) {
            min-width: 224px;
          }
        }

        .item-prices {
          order: 0;
          width: 100%;
        }
      }

      .item-list {
        @include mobile() {
          padding-left: 12px;
        }
      }

    }
  }

  // for checkout-items and options
  // Remove padding from stepper header & content area
  .checkout-tab-steps {
    .bs-stepper-header,
    .bs-stepper-content {
      padding: 0;
      margin: 0;
    }
  }

  // Checkout items list
  .checkout-items {
    .ecommerce-card {
      .item-img {
        img {
          width: 200px;
        }
      }

      // adding order as we have updated order for shop list view
      .item-name {
        order: 0 !important;
      }

      .item-company,
      .item-rating {
        margin-bottom: 0.4rem !important;
      }

      .item-quantity {
        display: flex;
        align-items: center;
      }

      .delivery-date {
        margin-top: 1.2rem;
        margin-bottom: 0.25rem;
      }

      // Item options button
      .item-options {
        .btn {
          display: flex;
          align-items: center;
          justify-content: center;
        }
      }
    }
  }

  .checkout-options {
    .coupons {
      &:focus-within {
        box-shadow: none;
      }

      input {
        border: none;
        padding-left: 0;
        color: $body-color;
        font-weight: $font-weight-bolder;

        &::placeholder {
          color: $body-color;
          font-weight: $font-weight-bolder;
        }
      }

      .input-group-append {
        margin: 0;
      }

      .input-group-text {
        height: auto;
        font-weight: $font-weight-bolder;
        padding: inherit;
      }
    }

    .price-details {
      .price-title {
        font-weight: $font-weight-bolder;
        margin-bottom: 0.75rem;
        margin-top: 1.5rem;
      }

      .price-detail {
        display: flex;
        justify-content: space-between;
        margin-bottom: 0.75rem;

        .detail-title {
          &.detail-total {
            font-weight: $font-weight-bolder;
          }
        }
      }
    }
  }

  .payment-type {
    .gift-card {
      cursor: pointer;
    }
  }

  // checkout wizard

  .checkout-tab-steps {
    background-color: transparent !important;
    box-shadow: none !important;

    .bs-stepper-header {
      border: none;
    }
  }

  // To hide toggler on lg and up and grid view for btn-wishlist and list view for checkout
  @include media-breakpoint-up(lg) {
    .ecommerce-header-items {
      // Hide toggler in > lg screen
      .shop-sidebar-toggler {
        display: none;
      }
    }

    // Checkout list view
    .product-checkout {
      &.list-view {
        grid-template-columns: 2fr 1fr;
        column-gap: 2rem;
      }
    }
  }

  /* products list */
  .ecommerce-filters {
    height: inherit;
    /*overflow: hidden;*/

    .grid-view {
      margin-top: 20px;

      .ecommerce-card {

        @include desktop() {
          padding: 20px 0 60px 0;
          margin-bottom: 40px;
        }

        &:after {
          content: "";
          position: absolute;
          height: 1px;
          border-bottom: 1px solid $borderSepColor;
          bottom: 40px;
          width: 110%;
          left: -1px;
          right: 4px;
          @include mobile() {
            width: 125%;
            left: -20px;
            right: -40px;
            bottom: 0;
          }
        }
      }
    }


    @include mobile() {
      .list-view {
        .ecommerce-card {
          &:after {
            content: "";
            position: absolute;
            height: 1px;
            border-bottom: 1px solid $borderSepColor;
            bottom: 40px;
            width: 110%;
            left: -1px;
            right: 4px;
            @include mobile() {
              width: 125%;
              left: -20px;
              right: -40px;
              bottom: 0;
            }
          }
        }
      }
    }
  }

}

.ecommerce_slider.ecommerce-application .grid-view {
  column-gap: 0 !important;
}

// Media queries to change grid columns in different Screens and to hide search results below 768px
@include media-breakpoint-down(xl) {
  .ecommerce-application {
    // Header item area
    .ecommerce-header-items {
      .btn-group {
        align-items: center;

        .btn-icon {
          padding: 0.6rem 0.736rem;
        }
      }
    }

    .grid-view.wishlist-items {
      grid-template-columns: 1fr 1fr 1fr;
    }

    // overlay style for e-commerce page
    .body-content-overlay {
      position: fixed;
      opacity: 0;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;

      &.show {
        opacity: 1;
      }
    }

    // Horizontal Menu scss
    &.horizontal-layout {
      .body-content-overlay {
        z-index: 998 !important;
      }

      .sidebar-shop {
        z-index: 999 !important;
      }
    }
  }
}

@include media-breakpoint-down(md) {
  .ecommerce-application {
    .sidebar-left {
      .sidebar {
        // removed radius and margin as showing sidebar in full height
        .card {
          border-radius: 0;
          margin: 0;
        }

        // Sidebar style
        .sidebar-shop {
          transform: translateX(-112%);
          transition: all 0.25s ease;
          position: fixed;
          top: 0;
          left: 0;
          height: 100%;
          overflow-y: scroll;
          margin: 0;

          &.show {
            transition: all 0.25s ease;
            transform: translateX(0);
          }
        }
      }
    }

    // Grid view for responsive
    .grid-view {
      grid-template-columns: 1fr 1fr;
    }

    // List View
    .list-view {
      .ecommerce-card {
        .ecommerce-card__body {
          grid-template-columns: 1fr;

        }
      }
    }

    .grid-view.wishlist-items {
      grid-template-columns: 1fr 1fr;
    }

    // to hide search results
    .ecommerce-header-items {
      .result-toggler {
        .search-results {
          display: none;
        }
      }
    }
  }
}

@include media-breakpoint-down(sm) {
  .ecommerce-application {
    .grid-view.wishlist-items {
      grid-template-columns: 1fr 1fr;
    }


  }

}

@include mobile() {
  .list-view {
    .ecommerce-card {
      .card-body {
        margin-top: 0;

        .item-name {
          margin-top: 2px !important;
        }

        .item-storage {
          height: auto;
          margin-bottom: 0;
        }
      }

      .item-buttons {
        margin-top: 5px;
      }

      .item-prices__mod {
        margin-bottom: 7px;
      }

      .item-prices {
        margin-bottom: 12px;
      }
    }
  }

}

@include media-breakpoint-up(sm) {
  .list-view {
    .ecommerce-card {

      .item-info {
        text-align: left;
        width: 100%;
        display: block;
        line-height: 16px;

        a {
          display: block;
          color: #666;
          height: 24px;
          font-size: 14px;

          &:hover {
            color: $primary;
          }
        }

        &__sep {
          display: block;
          border-top: 1px solid #EDEDED;
          max-width: 104px;
          margin-bottom: 13px;
        }
      }
    }
  }
}

.list-view {
  .ecommerce-card {
    &:first-child {
      border-top: 0;
    }

    .item-label {
      left: 20px;
      top: 39px;
      max-width: 244px;
      @include mobile() {
        left: 0;
      }
    }
  }
}


.item-img {
  user-select: none;
  /*    ,        X */
  touch-action: pan-y;
}

/* slider*/
.ecommerce_slider {
  &.swiper-container {
    padding-bottom: 20px;
  }

  .swiper-scrollbar-drag {
    background-color: #333333;
  }
}
