.checkbox-animated {
  font-size: 14px;
  font-weight: 300;
  line-height: 20px;
  position: relative;
  margin-top: 10px;
  margin-bottom: 10px;
  color: #000;
}

.checkbox-animated-custom {
  margin-bottom: 40px;
  margin-top: -20px;
}

.checkbox-animated input[type="checkbox"] {
  display: none;
}

.checkbox-animated label {
  font-weight: 400;
  position: relative;
  margin-bottom: 0;
  padding-left: 28px;
  cursor: pointer;
}

.checkbox-animated input[type="checkbox"]:checked ~ label .check {
  -webkit-transform: scale(1) rotate(45deg);
  transform: scale(1) rotate(45deg);
  opacity: 1;
}

.checkbox-animated label .check {
  z-index: 888;
  top: -7px;
  left: 6px;
  width: 12px;
  height: 24px;
  -webkit-transition-delay: 0.3s;
  transition-delay: 0.3s;
  -webkit-transform: rotate(180deg);
  transform: rotate(180deg);
  opacity: 0;
  border: 2px solid #bada55;
  border-top: none;
  border-left: none;
}

.checkbox-animated label[for="manager_check"] .check {
  z-index: 1;
}

.checkbox-animated input[type="checkbox"]:checked ~ label .box {
  -webkit-transform: scale(0) rotate(-180deg);
  transform: scale(0) rotate(-180deg);
  opacity: 0;
}

.checkbox-animated label span {
  position: absolute;
  left: -1px;
  display: block;
  -webkit-transition-duration: 0.3s;
  transition-duration: 0.3s;
}

.checkbox-animated label .box {
  z-index: 888;
  width: 20px;
  height: 20px;
  -webkit-transition-delay: 0.2s;
  transition-delay: 0.2s;
  border: 1px solid #d9d9d9;
  border-radius: 5px;
  background: #fff;
}

.checkbox-animated a {
  color: #428bca;
  font-weight: 500;
}

.ui-autocomplete {
  /* display: none; */
  position: absolute;
  z-index: 999;
  left: 0;
  top: 100%;
  width: 785px !important;
  padding-top: 10px;
  -webkit-box-shadow: 0 4px 4px 2px rgba(0, 0, 0, 0.2);
  box-shadow: 0 4px 4px 2px rgba(0, 0, 0, 0.2);
