@media screen and (min-width: 992px) {
  .home_brands .lazyload_img {
    min-height: 90px;
    min-width: 159px;
    max-height: 90px;
    max-width: 159px;
  }
}

.tob_banner_street_slide {
  opacity: 0;
  position: absolute;
  top: -35px;
  left: 0;
  width: 1000px;
  margin-left: -500px;
  text-align: center;
  transition: 0.3s;
}

.tob_banner_street_slide a {
  display: inline-block !important;
  font-size: 24px;
}

.tob_banner_street_slide.active {
  opacity: 1;
}


.ncatlist .ncatpaginator .page-link:not(.page-item) {
  position: relative !important;
  top: 14px !important;
}

.ncatlist .ncatpaginator .page-link:first-child {
  position: relative !important;
  top: 0px !important;
}

.ncatlist .mse2_pagination .ncatpaginator a:last-child {
  top: 0 !important;
}

.ncatlist .mse2_pagination .ncatpaginator span:last-child {
  top: 0 !important;
}


.ecommerce-filters_separator {
  /*  grid-row-start: 2;
    grid-row-end: 4;*/
  grid-column-start: 1;
  grid-column-end: 5;

  height: 1px;
  border-bottom: 1px solid #ccc;
  bottom: 40px;
  width: 100%;
  display: flex;
  margin-bottom: 20px;
}

.elem_search_choice_list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.ecommerce-application .ecommerce-filters .grid-view.empty_results {
  grid-template-columns: 1fr;
}

.ecommerce_slider.ecommerce-application .grid-view {
  column-gap: 0 !important;
}
